<script>
export let colNames;
export let dataRows;
export let numRows = 2;

$: displayRows = dataRows ? dataRows.slice(0, numRows) : null
</script>

<div>
{#if colNames && dataRows}
<table>
  <thead>
    <tr>
      {#each colNames as colName}
        <th>{colName}</th>
      {/each}
    </tr>
  </thead>
  <tbody>
    {#each displayRows as row}
      <tr>
        {#each row as cell}
          <td>
            {cell}
          </td>
        {/each}
      </tr>
    {/each}
  </tbody>
</table>
{/if}
</div>

<style>
</style>