<script>
  import { onMount } from "svelte";

  export let message = "Upload your file here"
  export let processFile = (filename, contents) => {}

  let hovering = false;
  let dragover = false;

  function handleMouseEnter(event) {
    event.preventDefault();
    hovering = true;
  }

  function handleMouseExit(event) {
    event.preventDefault();
    hovering = false;
    dragover = false;
  }

  function handleDragEnter(event) {
    event.preventDefault();
    dragover = true;
  }

  function handleDragOver(event) {
    event.preventDefault();
  }

  function handleDragLeave(event) {
    event.preventDefault();
    dragover = false;
    hovering = false;
  }

  function handleDrop(event) {
    console.log('handling drop')
    event.preventDefault();
    
    let file = event.dataTransfer.files[0];

    var reader = new FileReader();
    reader.onload = function(e) {
      console.log('Done reading file')
      var text = e.target.result;
      processFile(file.name, text)
    }
    reader.readAsText(file);

  }
</script>

<style>
  .dropzone {
    height: 200px;
    background: #fdfdfd;
    border-radius: 5px;
    border: 2px dashed #ff3e00;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 300ms ease-out;
  }
  .dropzone.hovering {
    background: #fff880;
  }
</style>

<div class="dropzone"
    class:hovering={hovering}
    on:dragmouseenter={handleMouseEnter}
    on:dragmouseexit={handleMouseExit}
    on:dragleave={handleDragLeave}
    on:dragenter={handleDragEnter}
    on:dragover={handleDragOver}
    on:drop={handleDrop}>

  <form class="my-form">
    <p>{message}</p>
  </form>

  <slot />
</div>
