<script>
	import Dropzone from './Dropzone.svelte';
	import Table from './Table.svelte';
	import GeocodeSection from './GeocodeSection.svelte';

	import Papa from 'papaparse'

	let filename;
	let contents;
	let data;
	
	$: {
		try {
			Papa.parse(contents, { 
				complete: function(results) {
					data = results.data
				}
			})
		} catch(err) {
			console.log("Could not parse")
		}
	}

	function processFile(newFilename, newContent) {
		filename = newFilename
		contents = newContent
	}

	$: colNames = data && data.length > 1 ? data[0] : null
	$: dataRows = data && data.length > 1 ? data.slice(1,10) : null
</script>

<main>
	<h1>Geocodarrr</h1>
	<Dropzone {processFile}></Dropzone>

	{#if filename}
		<h3>Preview {filename}</h3>
	{/if}
	{#if colNames}
		<Table {colNames} {dataRows}></Table>
		<GeocodeSection {colNames} {dataRows}></GeocodeSection>
	{/if}
</main>

<style>
	main {
		padding: 1em;
		max-width: 240px;
		margin: 0 auto;
	}


	@media (min-width: 640px) {
		main {
			max-width: none;
		}
	}
</style>