<script>
	import Fields from './Fields.svelte'

  export let colNames;

  let fields = [];
  export let name;
  export let process;
  let separator;

  $: process = row => {
    return [fields.filter(f => f.process)
                 .map(f => f.process(row))
                 .join(separator)]
  }

</script>

<div>
  <h3>Parameter: { name }</h3>
  <Fields {colNames} bind:fields={fields} bind:separator={separator}></Fields>
</div>

