<script>
  export let column;
  export let customText;
  export let process;
  export let isCustom;
  export let colNames;

  const customLabel = 'Custom text'

  $: isBlank = column == ''
  $: isCustom = column == customLabel
  $: process = row => {
    if (isCustom) {
      return customText
    } else {
      let colNum = colNames.indexOf(column)
      return row[colNum]
    }
  }
</script>

<div>
  <select bind:value={column}>
    <option value=''>Select a column</option>
    {#each colNames as col}
      <option value={col}>{col}</option> 
    {/each}
    <option value={customLabel}>{customLabel}</option>
  </select>
  {#if isCustom}
    <input type="text" placeholder="Comma, state, city, etc" bind:value={customText}>
  {/if}
  {#if !isBlank}
    <span on:click={() => column = ''}>[x]</span>
  {/if}
</div>

